import React from "react";
import { useFormContext } from "react-hook-form";
import { useSelector } from "react-redux";

// needed asssets

// svgs

// images
// needed styles
import styles from "./index.module.scss";

const EmploymentSubscriptionCard = ({ subType }) => {
  const { register } = useFormContext();
  const {
    meta: { locale },
  } = useSelector((state) => state);

  return (
    <label htmlFor={subType?._id} className={styles["label"]}>
      <input
        type="radio"
        value={subType?._id}
        id={subType?._id}
        {...register("type_id", { required: true })}
      />
      <div className={styles["container"]}>
        <div className={styles["header"]}>
          <h3>{locale === "ar" ? subType?.title_ar : subType?.title_en}</h3>
        </div>
        <div className={styles["content"]}>
          <p>{locale === "ar" ? subType?.content_ar : subType?.content_en}</p>
        </div>
        <div className={styles["price"]}>
          <span>السعر {subType?.cost} ج.م</span>
        </div>
      </div>
    </label>
  );
};

export default EmploymentSubscriptionCard;
