import { getCVSubscription } from "api/employee";
import React, { useEffect } from "react";
import { Container, Row } from "react-bootstrap";
import { FormProvider, useForm } from "react-hook-form";
import { FormattedMessage } from "react-intl";
import { useSelector } from "react-redux";
import { toast } from "react-toastify";

import EmploymentSubscriptionCard from "./EmploymentSubscriptionCard/EmploymentSubscriptionCard";

// needed styles
import styles from "./index.module.scss";

// needed components
const EmploymentSubscriptionPage = () => {
  const { subscriptionTypes } = useSelector((state) => state.empolyee);
  const methods = useForm({
    reValidateMode: "onSubmit",
  });
  const {
    handleSubmit,
    register,
    formState: { errors },
  } = methods;
  // handler Methods
  const submitHandler = async (data) => {
    const paymentUrl = await getCVSubscription(data);
    window.location.href = paymentUrl;
  };

  useEffect(() => {
    if (Object.keys(errors).length > 0) {
      toast.error(<FormattedMessage id="fill-required-input" />);
    }
  });
  return (
    <FormProvider {...methods}>
      <Container className={styles["container"]}>
        <Row className={styles["header"]}>
          <h1>اشتراك التوظيف</h1>
          <p>
            سيكون بإمكانك متابعة كل طلبات التوظيف لكل المهن شهريا والحصول على
            تحديثات دورية بالسير المهنية للمقدمين
          </p>
        </Row>
        <Row className={styles["card-section"]}>
          {subscriptionTypes.map((subType) => (
            <EmploymentSubscriptionCard {...{ subType }} />
          ))}
        </Row>

        <div className={styles["terms-and-conditions"]}>
          <label htmlFor="Terms-and-Conditions">
            <input
              type="checkbox"
              name="Terms-and-Conditions"
              id="Terms-and-Conditions"
              {...register("Terms-and-Conditions", { required: true })}
            />
            اوافق علي الشروط والاحكام
          </label>
          <form method="post" onSubmit={handleSubmit(submitHandler)}>
            <button className={styles["button"]}>المتابعة</button>
          </form>
        </div>
      </Container>
    </FormProvider>
  );
};

export default EmploymentSubscriptionPage;
